.firstContent_Background {
	background: url("../../images/illustration-editor-desktop.svg") no-repeat;
	background-position: right -250px center;
}
.firstContent {
	width: 100%;
	max-width: 1440px;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	font-family: "Overpass", sans-serif;
	align-items: center;
	padding-top: 40px;
	margin: auto;
	padding: 20px;
}
.firstContent h1,
.firstContent h2 {
	color: hsl(208, 49%, 24%);
}
.title {
	align-self: center;
	margin-top: 50px;
	/* transform: translateY(100px); */
	font-weight: 700;
	font-size: 40px;
	/* position: absolute; */
}
.firstContent_textContent {
	max-width: 600px;
	flex: 1;
}
.paragraph {
	font-size: 18px;
	letter-spacing: 1px;
	line-height: 30px;
	padding: 15px;
}
.textAndImageContainer {
	display: flex;
	position: relative;
	width: 50%;
	align-self: flex-start;
	justify-content: space-around;
	align-items: center;
	flex-wrap: wrap-reverse;
}
.imageDiv {
	flex: 1;
	min-width: 500px;
	justify-self: flex-end;
	transform: translateX(200px);
}
.imageDiv img {
	width: 100%;
	height: 100%;
}

@media (max-width: 1150px) {
	.textAndImageContainer {
		flex-direction: column;
		padding: 0 5%;
	}
	.imageDiv {
		transform: translateX(0px);
	}
	.firstContent {
		align-items: center;
		justify-content: center;
		text-align: center;
	}
	.firstContent_Background {
		background: url("../../images/illustration-editor-mobile.svg") no-repeat;
		background-position: center 20px;
	}
	.textAndImageContainer {
		width: 100%;
	}
	.placeholder {
		width: 100%;
		height: 400px;
	}
}

